/************************************** Views *****************************************************/

/* FHD or 4K */
@media (min-width: 2560px) {
  body {
    font-size: 22px; 
  }

  .container {
    padding: 50px; 
  }

  .logo{
    margin-left: -7.5%;
    width: 25%;
    margin-bottom: -8.4%;
  }

  nav.navbar .navbar-brand {
    width: 5%; 
  }

  nav.navbar .navbar-nav .nav-link.navbar-link {
    font-size: 30px; 
    padding: 0 30px; 
  }

  nav.navbar .navbar-nav {
    margin-left: 37%;
  }

  .social-icon{
    margin-left: 87.5%;
  }

  .social-icon a {
    width: 60px;
    height: 60px; 
    margin-top: 2%;
  }

  .social-icon a img {
    width: 60%; 
  }

  .navbar-nav{
    margin-top: -5%;
  }

  nav.navbar.scrolled {
    padding: 20px 0;
    background-color: #121212;
  }

  nav.navbar {
    padding: 0px 0;
    position: fixed;
    width: 100%;
    top: -18%;
    z-index: 99;
    transition: 0.32s ease-in-out;
  }

  .banner{
    margin-top: -5%;
  }
  
  .banner img{
    margin-top:-3%;
  }

  .project {
    margin-top: 1%;
  }

  /** 2k Modal **/
  .modal{
    height: 100%;
    width: 100%;
    font-size: 26px;
  }

  .modal{
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    margin-top: 10%;
