    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left:0; 
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 70%;
    z-index: 1000;
  }
  
  .modal-content h6{
    margin-top: 5%;
    font-size: 14px;
    color: #888;
  }
  
  .modal-content p{
    font-size: 18px;
    padding-top: 2%;
    line-height: 1.6;
    color: #555;
  }
  
  .modal-content h4{
    margin-top: 0;
    font-size: 24px;
    color: #333;
    justify-content:flex-end;
  }
  
  .modal-content h5{
    color: #333;
    text-decoration: underline;
    padding-top: 2%;
  }
  
  .modal-images{
    width: 100%;
    border-radius: 8px;
    margin-bottom: 15px;
  }
}

